<script>
import twoRow from '../modules/desk/other.vue'
import $ from 'jquery'
export default {
  data () {
    return {
       prizeList: [
        { name: "[2017]" },
        { name: "[2017]" },
        { name: "2016" },
        { name: "[2017]" },
        { name: "[2017]" },
        { name: "2016" },      
        { name: "[2017]" },
        { name: "[2017]" },
        { name: "[2017]" },
