      ],
      activeIndex: 0,
      demoList: [
        { name: 0 },
        { name: 1 },
        { name: 2 },
        { name: 3 },
        { name: 4 }
      ]
    }
  },
  components: {
    twoRow
  },
  computed: {
    top() {
      return - this.activeIndex * 40 + 'px';
    }
  },
  mounted() {
   var i = setInterval(_ => {
      if(this.activeIndex < this.demoList.length) {
        this.activeIndex += 1;
      } else {
        this.activeIndex = 0;
         if($(".scroll-content").scrollTop>=$(".scroll-content").scrollHeight/2){
          $(".scroll-content").scrollTop = 0;    
        }
      }
    }, 1000);
  },
  methods: {
    
  }
}
</script>

<style lang="less" scoped>
  @import '../style/common';
  @import '../style/content';
  .scroll-wrap{
  width: 200px;
  height: 50px;
  border: 1px solid blue;
  overflow: hidden;
}

</style>
<style lang="less">
  .el-input__inner {
    height: 30px;
  }
</style>


